@charset "utf-8";
/* header */
.header {
  width: 379px;
  height: 78px;
  background-color: #fff;
  padding: 0 26px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.language {
  display: flex;
  padding-left: 110px;
}
.language li a {
  display: block;
  font-size: 16px;
  color: #929191;
  font-weight: 700;
}
.language li:first-child a {
  padding-right: 15px;
  font-weight: 700;
  color: #000;
}
.menu {
  cursor: pointer;
  width: 30px;
  height: 30px;
  position: relative;
  margin-left: 0;
}
.line1,
.line3 {
  width: 30px;
  height: 4px;
  background-color: #000;
  position: absolute;
  top: 10%;
}
.line3 {
  top: auto;
  bottom: 11%;
}
.line2 {
  width: 15px;
  height: 4px;
  background-color: #000;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: 0.5s;
}
.main {
  width: 379px;
  height: 620px;
}
.family {
  width: 379px;
  height: 120px;
  background-color: #fff;
  padding: 10px 26px 36px;
}
.family li {
  font-family: "Noto Sans KR";
  font-size: 14px;
  padding: 6px 0;
  color: #dcdcdc;
  font-weight: 700;
}
.slideWrap {
  width: 379px;
  height: 450px;
  overflow: hidden;
  position: relative;
}
.slide1 {
  width: 4140px;
  height: 300px;
  position: absolute;
  top: -50px;
  left: 0;
  animation-name: aniLeft;
  animation-duration: 35s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

@keyframes aniLeft {
  0% {
    left: 0px;
  }
  100% {
    left: -3800px;
  }
}
.slide2 {
  width: 4648px;
  height: 300px;
  position: absolute;
  right: 0;
  bottom: 0;
  animation-name: aniRight;
  animation-duration: 30s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-fill-mode: forwards;
}
@keyframes aniRight {
  0% {
    left: 0px;
  }
  100% {
    left: -4100px;
  }
}
/* contents */
.grayBox {
  width: 379px;
  height: 20px;
  background-color: #ddd;
}
.intro {
  padding: 60px 26px;
  width: 379px;
  height: 470px;
  background-color: #fff;
  line-height: 1.6;
}
.intro h2 {
  letter-spacing: -3px;
  padding: 20px 0 30px;
  font-size: 35px;
  font-weight: 700;
}
.intro p {
  letter-spacing: -1px;
  font-size: 16px;
  font-weight: 500;
  padding-bottom: 50px;
}
.intro li a {
  color: #b63c22;
  font-size: 22px;
  font-weight: 700;
  letter-spacing: -1.5px;
}
.intro li a:hover {
  text-decoration: underline;
}
.intro li:last-child img {
  vertical-align: top;
  padding-right: 10px;
}
/* gallery */
.galleryBox {
  line-height: 1.6;
  padding: 60px 26px;
  font-family: "Noto Sans KR";
}
.galleryBox h3 {
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 18px;
  position: relative;
  margin-top: 30px;
  letter-spacing: -1px;
  cursor: pointer;
}
.galleryBox h3:hover a {
  background: linear-gradient(to right top, #0081cc, #009e38);
  color: transparent;
  -webkit-background-clip: text;
}
img {
  display: inline-block;
}
.arrowImg {
  padding-left: 14px;
  position: absolute;
  top: 14px;
}

/* list 안의 소제목 */
.galleryBox .list li {
  width: 325px;
  height: 614px;
  border-bottom: 1px solid #000;
  margin-bottom: 60px;
}
.galleryBox .list li p {
  width: 329px;
  letter-spacing: -0.5px;
  font-size: 16px;
}
/* 푸터 */
.footerWrap {
  width: 379px;
  height: 320px;
  background-color: #decdca;
  padding: 60px 26px;
}

.footerWrap li {
  width: 100%;
  height: 50px;
  letter-spacing: -2px;
  display: grid;
  justify-content: right;
  align-items: center;
  text-align: right;
  font-size: 28px;
  font-weight: 700;
}
.footerWrap li a:hover {
  color: #b63c22;
}
.copybox {
  width: 379px;
  height: 50px;
  background-color: #000;
  line-height: 50px;
  color: #797979;
  font-size: 12px;
  text-align: center;
}
.sns {
  width: 150px;
  padding: 40px 0 20px;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.informWrap {
  width: 379px;
  background-color: #111;
  text-align: center;
}
/* 이용약관 */
.check a {
  display: block;
  color: #666;
  font-size: 13px;
  padding: 8px 0;
  font-weight: 700;
  letter-spacing: -1px;
}
.check li:last-child a {
  color: #e5e5e5;
}
/* 랭귀지 */
.lang {
  display: flex;
  justify-content: center;
  padding: 20px 0;
}
.lang li:nth-child(2) {
  padding-left: 10px;
}
.lang li select {
  padding-left: 10px;
  width: 160px;
  height: 40px;
  font-size: 12px;
  color: #666;
  background-color: #111;
}
